*{
    box-sizing:border-box;
}
body{
    background-color: #f2f2f2;
    margin:0;
    padding:0;
    height:100%;
}

.movies{
    display:flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap:wrap;
    padding:50px;
    padding-top:70px;
    width:80%;
}

.movie{
    width:45%;
    background-color: white;
    margin-bottom:70px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    font-weight: 300;
    padding:20px;
    border-radius:5px;
    color:#adaeb9;
    box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),

}

.movie img{
    position:relative;
    top:-50px;
    width:100%;
    max-width:120px;
    margin-right:30px;
    box-shadow:0 13px 27px -5px rgba(50,50,93,0.25),
}
.movie__title,.movie__year{
    margin:0;
    font-weight: 300;
}

.movie__title{
    margin-bottom:5px;
    font-size:24px;
    color:#2c2c2c;
}

.movie__genres{
    list-style: none;
    padding:0;
    margin:0;
    display:flex;
    margin:5px 0px;
}

.movie__genres li,
.movie__year{
    margin-right:10px;
    font-size:14px;
}